.locations {
  margin-top: 96px;
  margin-bottom: 80px;

  h2 {
    font-size: $fz-base * 3;
    font-weight: 700;
    position: relative;
    padding-left: 20px;
    margin-bottom: 32px;

    &::before {
      position: absolute;
      content: "";
      width: 8px;
      height: 72px;
      top: 0;
      left: 0;
      background-color: $color-bg-dr;
      margin-right: 16px;
    }
  }

  .select-place {
    align-items: center;
    font-size: $fz-base * 1.5;
    gap: 16px;
    margin-bottom: 40px;

    select {
      padding: 6px 12px;
      font-size: $fz-base + 2px;
      min-width: 304px;
      border: 1px solid $color-selectBar;
    }
  }

  .store-items {
    width: 100%;
    flex-wrap: wrap;
    // justify-content: space-between;
    gap: 0 24px;

    &:nth-child(2) {
      padding-left: 24px;
      padding-right: 24px;
    }

    li {
      margin-bottom: 48px;
    }

    .store-card {
      width: 31%;

      h3 {
        font-size: $fz-base * 1.5;
        padding: 8px 24px;
        border: 1px solid #00000020;
      }

      .store-info {
        padding: 16px 24px;
        border: 1px solid #00000020;

        li {
          font-size: $fz-base + 4px;
          gap: 10px;

          &:not(:last-child) {
            margin-bottom: 8px;
          }
        }
      }

      .details {
        font-size: $fz-base + 3px;
        line-height: 27px;
        color: $color-text-wh;
        text-align: center;
        background-color: #000;
        padding: 16px;
      }
    }
  }
}

@media(max-width: 992px) {
  .locations {


    .store-items {
      gap: 24px;

      .store-card {
        width: 48%;

        .details {
          padding: 16px 130px;
        }
      }
    }

  }
}


@media(max-width: 767px) {
  .locations {
    margin-top: 40px;
    margin-bottom: 64px;


    h2 {
      font-size: $fz-base * 1.5;
      margin-bottom: 24px;
      text-align-last: left;

      &::before {
        height: 36px;
      }
    }

    .select-place {
      font-size: $fz-base ;
      gap: 8px;
      margin-bottom: 24px;

      select {
        width: 100%;
        min-width: auto;
      }
    }

    span {
      white-space: nowrap;
    }

    .store-items {
      .store-card {
        width: 100%;
      }
    }
  }
}